  @import url("https://fonts.googleapis.com/icon?family=Material+Icons");
  #demo .carousel-container {
    width: 100%;
    margin: 50px auto;
    min-height: 200px;
    position: relative;
  }
  
  #demo .carousel-container .carousel-inner {
    overflow: hidden;
  }
  #demo .carousel-container .track {
    width: 100%;
    display: inline-flex;
    transition: transform 0.5s;
  }
  #demo .carousel-container .card-container {
    width: 25%;
    flex-shrink: 0;
    height: 250px;
    padding-right: 15px;
    box-sizing: border-box;
  }
  @media (max-width:770px){
    #demo .carousel-container .card-container {
      width: 56%;
      flex-shrink: 0;
      height: 250px;
      padding-right: 15px;
      box-sizing: border-box;
    }
  }
  #demo .carousel-container .card-container .card {
    width: 100%;
    height: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
  }
  .nav button {
    border: 0;
    background-color: #f3f9ff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }
  .nav button i {
    font-size: 30px;
  }
  .nav .prev {
    left: -40px;
    display: none;
  }
  .nav .prev.show {
    display: block;
  }
  .nav .next {
    right: -40px;
  }
  .nav .next.hide {
    display: none;
  }
  .card > * {
    flex: 1;
  }
  .card .img {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
  }
  .img img{
    height: 100%;
  }
  .card .info {
    flex-basis: 40px;
    background: #222588;
    font-family: Verdana, Tahoma, sans-serif;
    color: #fff;
    flex-grow: 0;
    padding: 10px;
    box-sizing: border-box;
  }
  @media only screen and (max-width: 992px){
    #demo .carousel-container .card-container {
      width: 33.33%;
    }
  }
  @media only screen and (max-width: 720px){
    #demo .carousel-control-prev,.carousel-control-next{
      display: none;
    }
    #demo .carousel-container .card-container {
      width: 50%;
    }
  }
  @media only screen and (max-width: 420px){
    #demo .carousel-container .card-container {
      width: 100%;
    }
  }
  
